.test_field{
    display:flex;
    justify-content: space-evenly;
    margin-top: 20px;
}

.input_handler{
    border-radius: 5px;
    margin-bottom:5px;
}

.submit_handler{
    background-color: blue;
    color:white;
    border-radius: 5px;
    margin-bottom:5px;
    border: 1px solid darkblue;
    margin-left: 2px;
}

.submit_handler:hover{
    background-color: darkblue;
    cursor: pointer;
}

@media (min-width: 320px) and (max-width: 768px) {
    .input_handler{
        margin-left:20px;
    }
    .submit_handler{
        margin-left:22px;
    }
}


