.readout{
    margin-right:5px;
    display:flex;
    border-radius: 1px;
}

.items{
    color:white;
    background-color: blue;
    border-radius: 20px;
    border:1px solid blue;
    margin: 5px 5px;
    padding: 10px;
    box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
    animation: onLoad .5s ease-in 0s 1;
}

@media (min-width: 320px) and (max-width: 768px) {
    .items{
        height: 75px;
        animation: onLoad .5s ease-in 0s 1;
    }
    @keyframes onLoad {
        0%{
            transform:translateX(100%);
        }
        100%{
            transform: translateY(0);
        }
    }    
}
@keyframes onLoad{
    0% {
        transform: translateY(100%);
      }
      100% {
        transform: translateX(0);
      }
    }


h3.items:hover{
    cursor:pointer;
    border: 1px solid white;
}

.big_readout{
    display:flex;
    flex-direction: column;
    justify-content: space-evenly;
    margin-right:5px;
    border-radius: 1px;
    background-color: blue;
    border-radius: 20px;
    border:1px solid blue;
    box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
    animation: onLoad .5s ease-in 0s 1;

}

.single_item{
    color:white;
    height:100px;
    margin-top:50px;
    padding: 10px;
}

.back_badge{
    padding: 20px 20px;
    border-radius: 50%;
    font-size: 14px;
    width:15px;
    background: white;
    cursor:pointer;
}