.user_view{
    display:flex;
    justify-content: space-between;
}

.info_readout{
    display:flex;
    align-content: flex-start;
    flex-direction: column;
    width:300px;
    height:500px;
}

@media (min-width: 320px) and (max-width: 768px) {
    .user_view{
        display: flex;
        flex-direction: column;
        width:500px;
    }
    .info_readout{
        display:flex;
        flex-direction: column;
        width:100px;
        height:250px;
        overflow:auto;
    }
}
.items{
    background-color: crimson;
}
